<template>
    <header class="header">
        <router-link to="/new"
                     class="logo"
                     exact>
            <svg class="svg svg--navigation"
                 width="30px"
                 height="30px"
                 viewBox="0 0 256 256">
                <use xlink:href="#logo"
                     class="svg__use"></use>
            </svg>
            <span>patternCode</span>
        </router-link>

        <nav class="nav">
            <item-button v-bind:title="'New Pattern'"
                         v-on:click="handleNew"
                         v-bind:disabled="disabledNew">
                New
            </item-button>

            <!--  <router-link to="/Settings" class="nav__justify-right link-icon">
                <svg class="svg svg--settings"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#settings"></use>
                </svg>
            </router-link> -->
        </nav>

        <div class="search">
            <item-button class="button-menu"
                         v-bind:title="'Menu'"
                         v-bind:transparent="true"
                         v-on:click="handleMenu">
                <svg v-if="isMenuActive"
                     class="svg svg--close"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#close">
                    </use>
                </svg>
                <svg v-else
                     class="svg svg--menu"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#menu">
                    </use>
                </svg>
            </item-button>
        </div>
    </header>
</template>

<script>
import ItemButton from '../items/Button.vue';

export default {
    name: 'component-header',

    components: {
        ItemButton
    },

    data () {
        return {
            isMenuActive: false
        };
    },

    computed: {
        disabledNew () {
            if (this.$route.name === 'pattern-new') {
                return true;
            }

            const store = this.$store;

            if (!(store && store.state &&
                store.state.pattern &&
                store.state.pattern.current)) {
                if (!store.getters['pattern/current/edited']) {
                    return true;
                }
            }

            return false;
        }
    },

    methods: {
        handleNew () {
            this.$router.push({ path: '/new' });
        },

        handleMenu () {
            this.isMenuActive = !this.isMenuActive;
            this.$store.dispatch('aside/changeActive');
        }
    }
};
</script>

