<template>
    <dialog class="modal absolute"
            v-if="display">
        <div class="modal__box flex flex--vertical"
             v-if="typePatternSave">
            <div class="modal__box__left">
                <item-button v-on:click="closeModal">
                    Cancel
                </item-button>
                <item-button v-bind:disabled="!inputPatternName || inputError.usedName"
                             v-on:click="patternSave">
                    Save
                </item-button>
            </div>
            <div class="modal__box__right">
                <b>Name your pattern.</b>
                <item-input id="inputNameModal"
                            v-bind:error="inputError.display"
                            v-on:input="nameInputHandler"
                            v-model="inputPatternName"
                            autocomplete="off"
                            autofocus>
                </item-input>
                <label
                    v-if="typePatternSave && inputError.display && inputError.empty"
                    for="inputNameModal"
                    class="label label--error">
                    Filename can not be empty. Please choose an unique file name.
                </label>
                <label
                    v-if="typePatternSave && inputError.display && inputError.usedName"
                    for="inputNameModal"
                    class="label label--error">
                    Please choose an unique file name.
                </label>
            </div>
        </div>
        <div class="modal__box flex flex--vertical"
             v-if="typePatternDelete">
            <div class="modal__box__left">
                <item-button v-on:click="closeModal">
                    Cancel
                </item-button>
                <item-button v-on:click="patternDelete">
                    Delete
                </item-button>
            </div>
            <div class="modal__box__right">
                <b>Do you want to delete this pattern?</b>
                <p>This pattern will be lost and cant be restored!</p>
            </div>
        </div>
        <div class="modal__box flex flex--vertical"
             v-if="typeSectionRemove !== null">
            <div class="modal__box__left">
                <item-button v-on:click="closeModal">
                    Cancel
                </item-button>
                <item-button v-on:click="sectionRemove(typeSectionRemove)">
                    Remove
                </item-button>
            </div>
            <div class="modal__box__right">
                <b>Do you want to remove this section?</b>
                <p>This section content will be lost if you save your pattern!</p>
            </div>
        </div>
    </dialog>
</template>

<script>
import ItemButton from '../items/Button.vue';
import ItemInput from '../items/Input.vue';

export default {
    name: 'component-modal',

    components: {
        ItemButton,
        ItemInput
    },

    data () {
        return {
            inputPatternName: null,
            inputError: {
                display: false,
                empty: false,
                usedName: false
            }
        };
    },

    computed: {
        display () {
            return this.$store.getters['modal/display'];
        },

        typePatternSave () {
            return this.$store.getters['modal/typePatternSave'];
        },

        typePatternDelete () {
            return this.$store.getters['modal/typePatternDelete'];
        },

        typeSectionRemove () {
            return this.$store.getters['modal/typeSectionRemove'];
        }
    },

    methods: {
        async closeModal () {
            await this.$store.dispatch('modal/close');
        },

        async nameInputHandler () {
            // trim whitespace at start
            this.inputPatternName = this.inputPatternName.replace(/^\s+/g, '');
            // trim whitespace at end
            let trimmedPatternName = this.inputPatternName.replace(/\s+$/g, '');

            if (!this.inputPatternName || this.inputPatternName === '') {
                this.inputError.display = true;
                this.inputError.empty = true;
            } else {
                const nameAvailable = await this.$store
                    .getters['pattern/current/nameAvailable'](trimmedPatternName);

                if (!nameAvailable) {
                    this.inputError.display = true;
                    this.inputError.usedName = true;

                    trimmedPatternName = '';
                } else {
                    this.inputError.display = false;
                    this.inputError.empty = false;
                    this.inputError.usedName = false;
                }
            }

            await this.$store.dispatch('pattern/current/setName', {
                name: trimmedPatternName
            });
        },

        async patternSave () {
            console.log('save pattern');
            const url = await this.$store.dispatch('pattern/current/savePattern');

            if (url) {
                await this.$store.dispatch('modal/close');
                this.$router.push({ path: `/${url}` });
            }
        },

        async patternDelete () {
            await this.$store.dispatch('pattern/current/deletePattern');
            await this.$store.dispatch('modal/close');
            this.$router.push({ path: '/' });
        },

        async sectionRemove (data) {
            await this.$store.dispatch('pattern/current/removeSection', data);
            await this.$store.dispatch('modal/close');
        }
    }
};
</script>

