<template>
    <aside class="aside">
        <icons-edit></icons-edit>
        <nav class="nav">
            <item-button v-bind:transparent="true"
                         v-bind:disabled="hasPatternChanges"
                         v-on:click="handleSave"
                         title="Save">
                <svg class="svg svg--navigation" width="24" height="24" viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#save"></use>
                </svg>
            </item-button>
            <hr class="hr">
            <hr class="hr hr--last">
            <item-button v-bind:transparent="true"
                         v-bind:disabled="disabledDelete"
                         v-on:click="handleDelete"
                         title="Delete">
                <svg class="svg svg--navigation" width="24" height="24" viewBox="0 0 24 24">
                    <use class="svg__use"
                         xlink:href="#delete"></use>
                </svg>
            </item-button>
        </nav>
    </aside>
</template>

<script>
import ItemButton from '../items/Button.vue';
import IconsEdit from '../assets/icons/Edit.vue';

export default {
    name: 'component-aside-edit',

    components: {
        ItemButton,
        IconsEdit
    },

    computed: {
        /**
         * Watches if save button should be disabled or not.
         * It compares activePattern and newPattern and their section objects.
         * If objects are equal, it means there is nothing to save, so it should
         * return false.
         *
         * @method hasPatternChanges
         * @return {Boolean} Returns true if nothing to save
         */
        hasPatternChanges () {
            return this.$store.getters['pattern/current/edited'];
        },

        disabledDelete () {
            if (typeof this.$route.params.id !== 'undefined') {
                return false;
            }
            return true;
        }
    },

    methods: {
        async handleSave () {
            const url = await this.$store.dispatch('pattern/current/savePattern');

            if (url) {
                this.$router.push({ path: `/${url}` });
            }
        },

        async handleDelete () {
            await this.$store.dispatch('modal/openPatternDelete');
        }
    }
};
</script>

<style scoped>
.aside {
    grid-area: edit;
    display: flex;
    flex-direction: column;
}

.nav {
    position: fixed;
    top: calc(38px + 48px);
    right: 0;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 38px - 48px);
}

.hr {
    width: 60%;
    height: 1px;
    border: 0;
    background-color: var(--color-label);
}

.hr--last {
    margin-top: auto;
    margin-bottom: 5px;
}
</style>
