<template>
    <input class="input"
           v-bind:class="{
               'input--left': left,
               'input--error': error
           }"
           v-bind:type="type"
           v-bind:value="value"
           v-on:input="handleInput">
</template>

<script>
export default {
    name: 'item-input',

    props: {
        type: {
            type: String,
            default: 'text'
        },
        value: {
            type: String,
            default: ''
        },
        left: {
            type: Boolean,
            default: false
        },
        error: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        handleInput (event) {
            this.$emit('input', event.target.value);
        }
    }
};
</script>

<style scoped>
.input {
    width: 100%;
    height: var(--input-height-default);
    padding-top: 3px;
    padding-right: 8px;
    padding-bottom: 3px;
    padding-left: 8px;
    margin: 0;
    background-color: var(--color-background-main);
    border-width: 2px;
    border-style: solid;
    border-color: var(--color-yellow-dark);
    font-size: 12px;
    color: var(--color-text-default);
    outline: none;
}

.input:focus {
    border-color: var(--color-blue-dark);
}

.input--left {
    /* width: calc(100% + 2px); */
    /* margin-left: -2px; */
    border-left-width: 0;
}

.input--error {
    border-color: var(--color-red-dark);
}
</style>
