<template>
    <button class="button"
            v-bind:class="{
                'button--transparent': transparent,
                'button--pattern': pattern,
                'button--preview': preview
            }"
            v-bind:type="type"
            v-bind:title="title"
            v-bind:disabled="disabled"
            v-on:click="handleClick">
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: 'item-button',

    props: {
        type: {
            type: String,
            default: 'text'
        },
        title: {
            type: String
        },
        transparent: {
            type: Boolean,
            default: false
        },
        pattern: {
            type: Boolean,
            default: false
        },
        preview: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        handleClick () {
            this.$emit('click');
        }
    }
};
</script>

<style scoped>
.button {
    height: var(--input-height-default);
    padding-top: 5px;
    padding-right: 10px;
    padding-bottom: 5px;
    padding-left: 10px;
    margin-right: 4px;
    margin-left: 0;
    border-width: 2px;
    border-style: solid;
    border-color: var(--color-button-border-active);
    border-radius: 2px;
    outline: none;
    font-size: var(--font-size-app-small);
    line-height: 1;
    background-color: var(--color-button-background-active);
    color: var(--color-button-text-active);
    cursor: pointer;
}

.button--transparent {
    display: inline-block;
    height: 34px;
    border: 0;
    background-color: transparent;
    transition: all 0.25s ease-in-out;
}

.button--pattern {
    height: 24px;
    padding-top: 2px;
    padding-right: 4px;
    padding-bottom: 2px;
    padding-left: 4px;
}

button:disabled {
    border-color: var(--color-button-border-disabled);
    background-color: var(--color-button-background-disabled);
    color: var(--color-button-text-disabled);
    cursor: default;
}

.button:disabled use.svg__use {
    fill: var(--color-button-border-disabled);
}

.button--transparent:disabled {
    border-color: transparent;
    background-color: transparent;
}

button:last-of-type {
    margin-right: 0;
}

.button.button--preview use.svg__use {
    fill: var(--color-blue-dark);
}
